<template>
    <div class="myCourse">
        <el-table
             v-if="!showDetail"
            ref="multipleTable"
            :data="myCourse"
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column
                prop="serial"
                label=""
                width="80"
                align="center">
            </el-table-column>
            <el-table-column
                prop="courseName"
                label=""
                width="200"
                align="center">
            </el-table-column>
            <el-table-column
                label=""
                align="center">
                <template slot-scope="scope">
                    <el-button @click="changeCourse(scope.row)" type="text" size="small"></el-button>
                    <el-button @click="deleteCourse(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
            <el-table-column
                label=""
                align="center">
                <template slot-scope="scope">
                    <el-button @click="createClamp(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
            <el-table-column
                label=""
                align="center">
                <template slot-scope="scope">
                    <el-button @click="enterDetail(scope.row,0)" type="text" size="small"></el-button>
                    <el-button @click="enterDetail(scope.row,1)" type="text" size="small"></el-button>
                    <el-button @click="enterDetail(scope.row,2)" type="text" size="small"></el-button>
                    <el-button @click="enterDetail(scope.row,3)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <CourseDetail v-if="showDetail" @back="showDetail=false" :chooseContent="chooseContent" :handleType="handleType"></CourseDetail>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import CourseDetail from './courseDetail.vue'
export default {
    data(){
        return{
            showDetail:false,
            chooseContent:'',
            handleType:0
        }
    },
    components:{
        CourseDetail
    },
    computed:{
        ...mapGetters('teach',{
            myCourse:"getMyCourse",
            id:"getId",
        })
    },
    mounted(){
        this.requestMyCourse(this.id)
    },
    methods:{
        ...mapActions('teach',["requestMyCourse","requestCourseDetail","submitChangeCourse","submitDeleteCourse","submitCreateClamp"]),
        // 
        changeCourse(content){
            this.$prompt(`${content.courseName}`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(({ value }) => {
                    this.submitChangeCourse({content,that:this})
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: ''+err
                    });       
                });
        },
        // 
        deleteCourse(content){
            this.$confirm(`${content.name}?\n`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {
                    this.submitDeleteCourse({content,that:this})
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });
        },
        // 
        createClamp(content){
            this.$prompt(``, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                }).then(({ value }) => {
                    let name=value;
                    let that=this;
                    this.$prompt(`,`,'',{
                        confirmButtonText: '',
                        cancelButtonText: '',
                    }).then(({value})=>{
                        this.submitCreateClamp({name,classNo:value,id:content.courseInfoId,that});
                    }).catch(err=>{

                    })
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: ''+err
                    });       
                });
        },
        // 
        enterDetail(content,type){
            this.handleType=type;
            this.showDetail=true;
            this.chooseContent=content;
            this.requestCourseDetail({content,type})
        }
    }
}
</script>

