const namespaced = true;
import axios from 'axios'
const state={
    teachId:'',
    myCourse:[{
        courseInfoId:1,
        serial:1,
        courseName:''
    },{
        courseInfoId:2,
        serial:2,
        courseName:''
    },{
        courseInfoId:3,
        serial:3,
        courseName:''
    }],
    myClass:['1715431','1715432','1715433'],
    courseFile:[{
        courseFolderId:1,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:2,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:3,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715431'
    },{
        courseFolderId:4,
        courseName:'',
        courseFolderName:'',
        courseClass:'1715433'
    },],
    courseInfo: {
        name: "java",
        teachText: true,
        teachDemo: false,
        teachResourse: true,
        teachOthers: true,
        workReport: true,
        workExam: true,
        workOthers: false
    },
    detailFile:[{
        courseFileName: "1",
        courseFileId: ""
    },{
        courseFileName: "1",
        courseFileId: ""
    },{
        courseFileName: "1",
        courseFileId: ""
    },]
}
const getters={
    getId(state){
        return state.teachId
    },
    getCourseInfo(state) {
        return state.courseInfo
    },
    getMyCourse(state){
        return state.myCourse
    },
    getCourseFile(state){
        return state.courseFile
    },
    getMyClass(state){
        return state.myClass
    },
    getDetailFile(state){
        return state.detailFile
    }
}
const mutations={
    setTid(state,data){
        state.teachId=data;
    },
    setMyCourse(state,data){
        state.myCourse=data;
        for(let i=0;i<state.myCourse.length;i++){
            state.myCourse[i].serial=i+1;
        }
    },
    setCourseFile(state,data){
        state.courseFile=data;
    }
}
const actions={
    setTid({commit},id){
        commit("setId",id);
    },
    // 
    requestMyCourse({commit},id){
        axios.get(`/teacher/courseInfo/findListMyCourse?userInfoId=${id}`)
            .then(res=>{
                commit("setMyCourse",res.data.data)
            }).catch(err=>{

            })
    },
    // 
    submitChangeCourse({state,dispatch},{content,that}){
        axios.get(`/teacher/courseInfo/updateCourse?courseInfoId=${parseInt(content.courseInfoId)}&courseName=${content.courseName}`)
            .then(res=>{
                this.$message({
                    type: 'success',
                    message: ': ' + content.courseName
                });
                // 
                dispatch('requestMyCourse',state.teachId)
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
    },
    // 
    submitDeleteCourse({state,dispatch},{content,that}){
        axios.delete(`url?id=${[content.courseInfoId]}`)
            .then(res=>{
                that.$message({
                    type: 'success',
                    message: '!'
                });
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
    },
    // 
    submitCreateClamp({dispatch},{name,classNo,id,that}){
        classNo=classNo.split(',');
        axios.put(`/eacher/courseFolder/createFolder?folderName=${name}&courseClass=${classNo}&courseInfoId=${id}`)
            .then(res=>{
                that.$message({
                    type: 'success',
                    message: ''
                }); 
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                });
            })
    },
    // 
    requestFolder({commit,state}){
        axios.get(`/teacher/courseFolder/findListFolder?userInfoId=${state.teachId}`)
            .then(res=>{
                commit('setCourseFile',res.data.data)
            }).catch(err=>{

            })
    },
    // 
    requestCourseDetail({commit},{content,type}){
        // 0-> 1-> 2-> 3->
        axios.get(`url?id=${content.courseInfoId}&type=${type}`)
            .then(res=>{

            }).catch(err=>{

            })
    },
    // 
    requestDetail({commit},{id,type}){
        axios.get(`url?id=${id}&type=${type}`)
            .then(res=>{
                commit('changeDetail',res.data);
            }).catch(err=>{

            })
        },
    // 
    changeFolderData({dispatch},{content,that}){
        axios.put(`/teacher/courseFile/updateCourseFile?courseFileId=${content.courseFileId}&courseFileName=${content.courseFileName}`)
            .then(res=>{
                that.$message({
                    type: 'success',
                    message: ''
                }); 
            }).catch(err=>{
                that.$message({
                    type: 'error',
                    message: ''
                }); 
            })
    },
    // 
    deleteFolderData({state,dispatch},{content,that}){
        axios({
            url:'/teacher/courseFile/deleteDocument',
            method:'post',
            headers:{
                'deviceCode': 'A95ZEF1-47B5-AC90BF3'
            },
            data:{
                id:content.courseInfoId
            },
        })
        dispatch("")
    },
    // 
    downloadCourseFolder({state},{content,that}){
        let courseFolderName=content.courseClass+content.courseFolderName;
        axios({
            method:'get',
            url:`/teacher/courseFile/downloadHomewordFile?courseFolderId=${parseInt(content.courseFolderId)}&courseFolderName=${courseFolderName}`,
            responseType: 'blob'
        }).then(res=>{
            if(res.data){
                const a = document.createElement('a');
                a.download = `${courseFolderName}.zip`;
                a.href =`/teacher/courseFile/downloadHomewordFile?courseFolderId=${parseInt(content.courseFolderId)}&courseFolderName=${courseFolderName}`;
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
                that.$message({
                    type: 'success',
                    message: ''
                }); 
            }else{
                that.$message({
                    type: 'error',
                    message: ''
                });    
            }
        })
    }
}

export default {namespaced,state,getters,mutations,actions}