const namespaced = true;
import axios from 'axios';
const state = {
    stuId:'',
    courseInfo: {
        courseName: "java",
        teacherName:'',
        teachingDocument: '',
        teachingCase: '',
        teachingResources: '',
        otherTeachingResources: '',
        experimentalReportTemplate: '',
        dailyWorkTemplate: '',
        otherTermAssignments: ''
    },
    searchContent:[{
        courseInfoId:1,
        teacherName:'',
        courseName:''
    },{
        courseInfoId:2,
        teacherName:'',
        courseName:''
    }],
    detailFile: [{
        courseFileName: "1",
        courseFileId:'',
        src: ""
    }],
    updateTable:[{
        courseInfoId:'',
        courseFolderId:'',
        courseFolderName:'',
    },{
        courseInfoId:'',
        courseFolderId:'',
        courseFolderName:'',
    },{
        courseInfoId:'',
        courseFolderId:'',
        courseFolderName:'',
    },{
        courseInfoId:'',
        courseFolderId:'',
        courseFolderName:'',
    }]
}

const getters = {
    getId(state){
        return state.stuId
    },
    getCourseInfo(state) {
        return state.courseInfo
    },
    getDetailFile(state){
        return state.detailFile
    },
    getSearchContent(state){
        return state.searchContent
    },
    getUpdateTable(state){
        return state.updateTable
    }
}

const mutations = {
    setSId(state,id){
        state.stuId=id
    },
    changeUpdateTable(state,data){
        state.updateTable=data;
    },
    changeSearchContent(state,data){
        state.searchContent=data
    },
    changeInfo(state, {data,content}) {
        state.courseInfo = data;
        let keys=Object.keys(state.courseInfo);
        for(let i=0;i<keys.length;i++){
            if(state.courseInfo[keys[i]]>0){
                state.courseInfo[keys[i]]=''
            }else{
                state.courseInfo[keys[i]]=''
            }
        }
        state.courseName=content.courseName;
    },
    changeDetail(state,data){
        state.detailFile=data
    }
}

const actions = {
    setSId({commit},id){
        commit('setSId',id)
    },
    // 
    requestUpdateTable({commit},{course,classNo}){
        axios.get(`/student/courseFolder/read?&courseName=${course}&courseClass=${classNo}`)
            .then((res) => {
                commit('changeUpdateTable', res.data.data)
            }).catch((err) => {
                console.log(`the error happen because of ${err}`)
            })
    },
    // 
    requestSearchContent({commit},{teacher,course}){
        axios.get(`/student/courseInfo/search?teacherName=${teacher}&courseName=${course}`)
            .then(res=>{
                commit("changeSearchContent",res.data.data);
            }).catch(err=>{

            })
    },
    // 
    requestInfo({ commit }, { content }) {
        axios.get(`/student/courseFile/read?courseInfoId=${content.courseInfoId}`)
            .then((res) => {
                commit('changeInfo', {data:res.data,content})
            }).catch((err) => {
                console.log(`the error happen because of ${err}`)
            })
    },
    // 
    requestDetail({commit},{id,type}){
        axios.get(`/student/courseFile/to?courseInfoId=${id}&courseFileType=${type}`)
            .then(res=>{
                commit('changeDetail',res.data.data);
            }).catch(err=>{

            })
    }
}
export default { state, getters, mutations, actions, namespaced }