<template>
    <div>
        <el-drawer
        title=""
        :visible.sync="drawer"
        :direction="direction"
        :before-close="handleClose">
            <el-upload
                class="upload-demo"
                action="/teacher/courseFile/fileUpload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :data="{courseInfoId:parseInt(chooseContent.courseInfoId),courseFileType:parseInt(type)}"
                :limit="3"
                :on-exceed="handleExceed"
                :file-list="fileList">
                <el-button size="small" type="primary"></el-button>
            </el-upload>
        </el-drawer>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        fileList: [],
        direction: 'ttb',
      };
    },
    props:["drawer","chooseContent","type"],
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  }
</script>

