<template>
    <div class="fileDownload">
        <h1 class="courseName">{{courseInfo.name}}</h1>
        <span class="source">{{type | parentName}}>{{type | childName}}</span>
        <div class="handleBtn">
            <el-button type="primary" round >{{handleType | setHandle}}</el-button>
            <el-button type="info" round @click="$emit('back')"></el-button>
        </div>
        <div class="downloadDetail">
            <el-table
                ref="multipleTable"
                :data="detailFile"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="courseFileName"
                    label=""
                    width="600"
                    align="center">
                </el-table-column>
                <el-table-column
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">{{handleType | setHandle}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

export default {
    computed:{
        ...mapGetters("teach",{
            courseInfo:"getCourseInfo",
            detailFile:"getDetailFile"
        })
    },
    props:["handleType","type"],
    filters:{
        parentName:function(val){
            if(val<=4){
                return ''
            }else{
                return ''
            }
        },
        childName:function(val){
            let arr=['','','','','','',''];
            return arr[val-1];
        },
        setHandle:function(val){
            if(val===0){
                return ''
            }else if(val===1){
                return ''
            }else if(val===2){
                return ''
            }else if(val===3){
                return ''
            }
        },
    },
    methods:{
        ...mapActions('teach',['changeFolderData','deleteFolderData']),
        // 
        handleClick(content){
            if(this.handleType===2){
                 this.$prompt(`${content.courseName}`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(({ value }) => {
                    if(value===content.courseName){
                        this.$message({
                            type:'info',
                            message:''
                        })
                    }else{
                        this.changeFolderData({content,that:this});
                    }
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: ''+err
                    });       
                });
            }else if(this.handleType===3){
                this.deleteFolderData({content,that:this});
            }
        }
    }
}
</script>

<style lang="less" scoped>
.fileDownload{
    .courseName{
        font-size: 18px;
    }
    .handleBtn{
        float: right;
    }
}
</style>
